/* =================================================================================
    LOGO
================================================================================= */
.logo {
  background: url('../images/inwi.png') no-repeat;
  -webkit-background-size: contain;
  background-size: contain;
  display: flex;
  width: 150px;
  height: 35px;
}

/* =================================================================================
    LOGIN VIEW
================================================================================= */
.login-card {
  @include shadow(50px, map-get($color, "normal-gray"));
}

/* =================================================================================
    HEADER
================================================================================= */
// header and sidebar opacity
$head_side_opacity: .9;
$head_side_bg: 'head';
header {
  height: 60px;
  background: rgba(map-get($color, $head_side_bg), $head_side_opacity);

  .user-info {
    .btn {
      @include no-radius;
      height: 60px;
      min-width: 61px;
    }

    .btn-link:hover,
    .btn-link:active,
    .btn-link:focus {
      text-decoration: none;
    }
  }
}

/* =================================================================================
    SIDEBAR
================================================================================= */
.sidebar {
  //max-width: 5.333337%;
  //width: 5.333337%;
  background: rgba(map-get($color, 'sidebar-bg'), 1);
  //background: linear-gradient(#{map-get($color, 'layer2')}, transparent);

  ul {

    li {
      background-color: transparent;
      box-sizing: border-box;

      &.list-group-item {
        padding: 0;
        border:none;

        &.disabled,
        &:disabled {
          background-color: transparent;
        }
      }

      a {
        display: block;
        width: 100%;
        font-size: 20px;
        padding: 10px 0;
        text-align: center;
        color: map-get($color, "sidebar-text");
        box-sizing: border-box;

        -webkit-transition: all .2s;
        -moz-transition: all .2s;
        -ms-transition: all .2s;
        -o-transition: all .2s;
        transition: all .2s;
      }

      &:hover {

        a {
          &:hover,
          &:focus {
            -webkit-transform: scale(1.4);
            -moz-transform: scale(1.4);
            -ms-transform: scale(1.4);
            -o-transform: scale(1.4);
            transform: scale(1.4);
            color: rgba(map-get($color, "sidebar-text"), .7);
          }
        }
      }
    }
  };
}

/* =================================================================================
    CONTENT AREA
================================================================================= */
.content {
  .fixedCard {
    height: calc(100vh - 125px);
  }
  .fixedCardHelpPage {
    height: calc(100vh - 90px);

    iframe {
      border: none;
    }
  }
}

.maximized {
  .fixedCard {
    height: calc(100vh - 65px);
  }
}

.toolbar {
  .card-body {
    padding: .4rem;
  }
}

.content,
.maximized {
  .fixedCard {
    .card-body {
      padding: .25rem;
    }
    .card-footer {
      padding: .25rem;

      .pagination {
        margin: 0;
      }
    }
  }

  //Topology View Headers
  .topologyViewHeader{
    line-height: 20px;
    padding: .3rem;
    margin: .2rem .2rem .05rem .2rem;
    border-bottom: 1px map_get($color, 'normal-gray') solid;
  }
}

// Empty Data matrix
.empty-data {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(255,255,255,.8);
  z-index: 160;

  .empty-data-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.pagination {
  display: inline-flex;

  li {

    &.disabled {
      a {
        pointer-events: none;
        color: map_get($color, 'normal-gray');
        background-color: map_get($color, 'layer1');
      }
    }

    &:not(.disabled) a:hover {
      color: map_get($color, 'brand-white');
      background-color: map_get($color, 'brand-blue');
      border-color: map_get($color, 'brand-blue');
    }

    a {
      position: relative;
      display: block;
      padding: 0.5rem 0.75rem;
      margin-left: -1px;
      line-height: 1.25;
      color: map_get($color, 'brand-blue');
      background-color: map_get($color, 'brand-white');
      border: 1px solid map_get($color, 'light-gray');
    }

    &.active > a {
      z-index: 3;
      color: map_get($color, 'brand-white');
      background-color: map_get($color, 'brand-blue');
      border-color: map_get($color, 'brand-blue');
    }
  }
}

/* =================================================================================
   HandsonTable Override
================================================================================= */
.htCore tbody td {
  min-width: 70px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;

  &.revision-row {
    color: map-get($color, 'normal-gray');
    text-decoration: line-through;
    background-color: map-get($color, 'layer1');
  }

  &.htInvalid {
    background-color: rgba(map-get($color, 'brand-red'), 1) !important;
    color: map_get($color, 'brand-white') !important;
  }
}



.dropdown-item {
  cursor: pointer;
}

/* =================================================================================
    CONTENT AREA --- MESSAGE BOX
================================================================================= */
.react-confirm-alert {
  .card {
    @include shadow(15px, map_get($color, 'light-gray'));

    .card-body {
      padding: 35px;
    }
  }
}
.react-confirm-alert-overlay {
  z-index: 199 !important;
  background-color: rgba(255, 255, 255, 0.5) !important;
}

/* =================================================================================
HELPER CLASSES
================================================================================= */
#hot-display-license-info {
  display: none;
}
.http-status-bg {
  font-size: 45vh;
  color: rgba(25, 55, 75, .1);
  transform: rotate(-62deg);
  position: fixed;
  top: 10%;
  right: 5vh;
}
/* =================================================================================
VIS JS OVERRIDE CLASSES
================================================================================= */
.vis-network {

  &:focus {
    outline: none;
  }

  .vis-navigation {

    .vis-button{
      width: 36px !important;
      height: 36px !important;
      background-position: center !important;

      -webkit-transform: scale(.7);
      -moz-transform: scale(.7);
      -ms-transform: scale(.7);
      -o-transform: scale(.7);
      transform: scale(.7);

      &.vis-zoomIn {
        background-image: url('data:image/png;base64,') !important;
        bottom: 0px !important;
        right:0px !important;
      }

      &.vis-zoomOut {
        background-image: url('data:image/png;base64,') !important;
        bottom: 0px !important;
        right: 30px !important;
      }

      &.vis-zoomExtends {
        background-image: url('data:image/png;base64,') !important;
        bottom:25px !important;
        right:0px !important;
      }

      &.vis-up {
        background-image: url('data:image/png;base64,') !important;
        bottom: 20px !important;
        left: 30px !important;
      }

      &.vis-down {
        background-image: url('data:image/png;base64,') !important;
        bottom: -5px !important;
        left: 30px !important;
      }

      &.vis-left {
        background-image: url('data:image/png;base64,') !important;
        bottom: 0px !important;
        left: 10px !important;
      }

      &.vis-right {
        background-image: url('data:image/png;base64,') !important;
        bottom: 0px !important;
        left: 50px !important;
      }

      &:hover, &:active {
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.3) !important;
      }
    }
  }
}

/* =================================================================================
IMPORT MODAL
================================================================================= */
.import-file {
  .choose-file-wrapper {
    border: 1px solid map_get($color, "layer1");
    display: block;
    width: 100%;
    -webkit-transition: all .3s;
    -moz-transition: all .3s;
    -ms-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    background: transparent;
    text-align: center;

    .chose-file {
      background: transparent;
      border: none;
      position: relative;
      width: 100%;
      height: 100%;
      font-size: 25px;
      line-height: 3em;
      color: map_get($color, "brand-white");
      background: map_get($color, "brand-blue");

      &:active,
      &:focus,
      &:visited {
        color: map_get($color, 'layer2') !important;
      }
    }
    h4 {
      line-height: 3em;
      color: map_get($color, 'normal-gray')
    }
    &:hover {
      border-color: map_get($color, "layer2");
    }
  }

  .icon {
    vertical-align: top;
  }

  .progressWrapper {
    height: 30px;
    margin: 10px 0 10px 0;
    width: 100%;
    overflow: hidden;
    position: relative;
    border-radius: 0;
    background-color: map_get($color, "layer2");
    -webkit-box-shadow: inset 0 1px 2px rgba(0, 0, 0, .1);
    -moz-box-shadow: inset 0 1px 2px rgba(0, 0, 0, .1);
    box-shadow: inset 0 1px 2px rgba(0, 0, 0, .1);

    span {
      position: absolute;
      z-index: 1;
      color: white;
      transform: translateX(-50%);
      top: 5px;
    }

    .progressBar {
      position: absolute;
      float: none;
      height: 100%;
      background-color: map_get($color, "brand-green");
      -webkit-box-shadow: inset 0 0 20px rgba(0, 0, 0, .15);
      -moz-box-shadow: inset 0 0 20px rgba(0, 0, 0, .15);
      box-shadow: inset 0 0 20px rgba(0, 0, 0, .15);

      -webkit-transition: width .6s ease;
      -moz-transition: width .6s ease;
      -ms-transition: width .6s ease;
      -o-transition: width .6s ease;
      transition: width .6s ease;
    }
  }
}

/* =================================================================================
SPINNER
================================================================================= */
.spinner{
  background-color: rgba(map_get($color,'layer0'),.5);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 15000;

  img {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }
}

